.promo-slider{
	position: relative;
	
	padding: 0 15px;
	max-width: 1790px;
	margin: 0 auto;
}
.promo-slider__item-inner{
	display: flex;
}
.promo-slider__content{
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.promo-slider__content-inner{
	max-width: 60%;
}
.promo-slider__controls{
	position: absolute;
	left: percentage(705/1760);
	bottom: 0;
	top: 0; bottom: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	z-index: 100;
}
.promo-slider__controls-prev,
.promo-slider__controls-next{
  border: none;
  outline: none;
  background: transparent;
}
.promo-slider__pagination{
	width: 27px;
	margin: 70px auto;
	font-size: 22px;
	font-weight: 400;
	line-height: 52px;
	letter-spacing: 1.1px;
	text-align: center;
	color: #000000;
}
.promo-slider__pagination-current{}
.promo-slider__pagination-divider{}
.promo-slider__pagination-total{}
.promo-slider__photo{}

.promo-slider__title{
	position: relative;
	display: inline-block;
	padding-bottom: 42px;
	margin-bottom: 36px;
	&:after{
		content: '';
		position: absolute;
		bottom: 0; left: 0;
		width: 80px;
		height: 6px;
		background-color: #004d8f;
	}
}
.promo-slider__title-text{
	font-size: 59px;
	font-weight: 900;
	line-height: 50px;
	color: #010101;
}
.promo-slider__title-type{
	font-size: 20px;
	font-weight: 600;
	line-height: 24px;
	text-align: right;
	color: rgba(#010101, .5);
}
.promo-slider__title-model{
	font-size: 20px;
	font-weight: 600;
	line-height: 20px;
	color: #010101;
}
.promo-slider__text{
	font-size: 16px;
	font-weight: 400;
	line-height: 26px;
	color: rgba(#000000, .5);
}
.promo-slider__action{
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
}
.promo-slider__price{
	display: flex;
	align-items: flex-end;
}
.promo-slider__price-value{
	font-size: 69px;
	font-weight: 900;
	line-height: 52px;
	color: #000000;
}
.promo-slider__price-currency{
	margin-left: 24px;
	font-size: 22px;
	line-height: 22px;
	font-weight: 400;
	color: #38373c;
}
.promo-slider__readmore{
	display: inline-block;
	padding: 25px 62px;
	margin: auto;
	background-color: $color__primary;
	font-size: 20px;
	font-weight: 700;
	line-height: 20px;
	letter-spacing: 0.7px;
	text-transform: uppercase;
	text-align: center;
	color: #ffffff;
}